html,
body {
  width: 100%;
  height: 100%;
  overflow: hidden;
}

body {
  background-color: var(--seacolor);
  margin: 0;
  padding: 0;
}

#root {
  width: 100%;
  height: 100%;
  position: relative;
}

.app__container {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  box-sizing: border-box;
  padding-top: 20px;
  padding-bottom: 90px;
}

.vertical {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
}

/* Desktop vertical container */
@media (min-width: 1024px) {
  .vertical {
    max-width: 900px;
  }
}

.prev,
.next {
  position: absolute;
}

.prev {
  left: 0;
}

.next {
  right: 0;
}

.copyright {
  color: rgba(255, 255, 255, 0.7);
  font-size: 0.7rem;
  margin-bottom: 0.5rem;
}
