.home__container {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  box-sizing: border-box;
}

.home__title {
  font-size: 2rem;
  color: white;
  margin: 3rem 0 1rem 0;
  text-align: center;
}

/* Desktop title styling */
@media (min-width: 768px) {
  .home__title {
    font-size: 2.5rem;
    margin: 4rem 0 2rem 0;
  }
}

.home-viewtype {
  border: none;
  background-color: transparent;
  color: white;
}

.home-viewtype:hover {
  cursor: pointer;
}
